<template>
    <div>
          
        <div @click="$store.commit('toggleLeftMenu','')"  class="hidden w-full sidebar-bg-close bg-theme_secondary text-theme_primary opacity-25" ></div>
  
         <nav class=" bg-theme_primary_dark neu-push content-start hidden fixed top-0 lg:flex w-2/3 lg:w-1/5 xl:w-1/6 p-5 flex-wrap sidebar font-semibold">                                
            <div class="w-full flex flex-wrap">   
                    <div class="rounded-lg p-1">
                        <div class="flex  rounded-lg px-3 py-5 flex-wrap">

                             <router-link class="flex text-primary text-md mt-1 items-start" to="/">
                                <div class="rounded-full bg-primary border-2  border-secondary">
                                    <img class="w-8 h-8" src="@/assets/logo-bootwind-template.png" alt="Logo BootWind Template">
                                </div>
                                <span class="ml-4">
                                    Mahasiswa 
                                    <br> <small> Aktif </small>
                                </span> 
                            </router-link>

                               <ul class="w-full mt-5">
                                   <li v-for="(menu,i) in $store.state.listMenu" :key="i+menu.path">
                                        <span v-if="!menu.path" class="border-t flex w-full border-primary font-semibold py-3 px-4 text-primary mt-2">
                                            {{ menu.name }}
                                        </span>
                                        <router-link v-if="menu.path" :to="menu.path" :class="($route.path == menu.path) ? ActiveClass : nonActiveClass">
                                            {{ menu.name}}
                                        </router-link>
                                        <template v-if="menu.sub">
                                            <ul v-for="subMenu in menu.sub" :key="subMenu.path" class="rounded-lg w-full ">
                                                <li :class="($route.path == subMenu.path) ? ActiveClass : nonActiveClass">
                                                    <router-link :to="subMenu.path" class="flex w-full">
                                                       {{ subMenu.name}}
                                                    </router-link>
                                                </li>
                                            </ul>
                                        </template>
                                   </li>
                               </ul>
                              
                        </div>
                    </div>
            </div>
        </nav>
    </div>
</template>

<script>
export default {
    data(){
        return {
            nonActiveClass : 'flex w-full hover:bg-theme_primary hover:font-semibold rounded-lg py-2 px-4 hover:text-primary my-1',
            ActiveClass : 'flex w-full bg-theme_primary font-semibold rounded-lg py-2 px-4 text-primary my-1'
        }
    }
}
</script>